// feste Bildgre
$gallery-image-width-lg: 60px;
$gallery-image-width-xl: 73px;

.product-gallery {

  .product-gallery-img {
    cursor: pointer;
  }

  @include media-breakpoint-only(lg) {
    width: $gallery-image-width-lg;
  }
  @include media-breakpoint-up(xl) {
    width: $gallery-image-width-xl;
  }

  .slick-slide img{
    min-height: remCalc(20);
  }
}

.product-carousel-prev {
  left: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}

.product-carousel-next {
  right: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}

.sumbox-carousel-prev {
  left: 0;
  top: calc(50% - 47px);
  opacity: 0.7;
}

.sumbox-carousel-next {
  right: 0;
  top: calc(50% - 47px);
  opacity: 0.7;
}

.product-image-container {
  max-width: remCalc(468);
}

.skeleton-image {
  width: 100%;
  padding-bottom: calc(100% / (2 / 3));

  > img {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
